 $(function () {
     $('.select').on('click', function(){
         $('.select__dropdown').toggleClass('select__dropdown-open');
     });
     $('.select__option').on('click', function(){
         var selectValue = $(this).attr('data-value');
         $('#select-type').val(selectValue);
         $('.select_cheked').text(selectValue);
     });
 });


